.trip-container {
  width: 100%;
  height: 100vh;
  position: relative;
}

.time {
  position: absolute;
  top: 15px;
  left: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  z-index: 10;
}

#slider {
  position: absolute;
  bottom: 30px;
  left: 50px;
  right: 50px;
  z-index: 10;
}
